<template>
  <section id="divMainAssetGroupListingData" class="main-content no-left-bar">
    <!-- <loader :is-visible="isLoading"></loader>        -->
    <div class="right-content">
      <div class="col-md-12 p-0">
        <div class="theme-primary partition tablehead p-actions-expand">
          <span class="p-name text-white">{{ $t('ReturnList') }}</span>
          <span class="collapse-head-btns"><a href="javascript:;" class="collapse-btns"><i
                class="fa fa-angle-up text-white"></i></a></span>
        </div>
      </div>
      <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left p-0">
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left pt-2 p-0">
          <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
            <div class="row mt-0">
              <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                <div class="form-group">
                  <input class="form-control" type="text" placeholder="Search by EPC Tag or Name or Unique Name"
                    v-model="search" autocomplete="off" name="search" />
                </div>
              </div>
              <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                <div class="search-btm-btn background-0 text-left form-check-inline m-0">
                  <a href="javascript:;" title="Search" class="btn btn-success mr-1 px-4" @click="SearchData"><em
                      class="fa fa-search text-white"></em></a>
                  <a title="Clear" href="javascript:;" class="btn btn-danger clearsearchtext px-4" @click="ResetData"><em
                      class="fa fa-refresh"></em></a>
                  &nbsp;
                  <a id="Search_rfid" @click="ReturnList()" href="javascript:;" class="btn btn-info" :title="$t('RFIDCheckinItems')"
                    data-toggle-tooltip="tooltip" data-original-title="RFID Checkin Items"><i
                      class="fa fa-barcode text-white float-left mt-1"></i><span class="text-white mx-2">{{
                        $t('RFIDCheckinItems') }}</span></a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div style="clear: both;"></div>
    <tg-list :IsShowAction="false"  SearchPosition='none'  :listType="['List']" ModuleName="Asset" SubModuleCode="Asset" IdentityColumn="EPC_TAG" :PageSize="PageSize"
      HeaderText="Return List" :ListData="returnProductData" :HeaderData="Headers" :LegendArray="LegendArray" :widgets="widgets" :SearchFields="leftSearchFields"
       :RenderRowActionMethod="renderActions" :TotalRecords="TotalRecords" :ListDataCallBackFunction="GetReturnList"
      :NorecordfoundText="$t('NorecordfoundText')"  v-on:PagerButtonClick="pagerMethod"
       v-on:SortdataButtonClick="sortdata">
      <template v-slot:slotdata="{ data }">
        <template v-if="data.column.COLUMN_NAME == 'Return'">
          <td class="text-center single-action" v-if="data.row.number_of_items_assigned > 0">
            <span class="text-dark">
              <em v-on:click="AssetMutipleReturn(data.row)" class="table-head-btn  iconenable">
                <i class="fa fa-undo"></i><span>{{ $("Return") }}</span></em>
            </span>
          </td>
          <td v-else class="text-center single-action">
            <span class="active-disabled">
              <span class="text-center">{{ $t('WaitingforAcceptance') }}</span>
            </span>
          </td>
        </template>
      </template>
    </tg-list>
  </section>
</template>
<script>




import DataService from "../../service/DataService";
export default {

  data() {

    return {
      search: "",
      leftSearchFields:[],
      widgets:[],     
      listheaderbuttons:[],
      LegendArray:[],
      chekedInId: "",
      sortBy: "",
      sortExp: "",
      returnProductData: [],
      noRecord: false,
      TotalRecords: 0,
      pagenumber: 100,
      pageSize: 0,
      CurrentPage: 1,
      isLoading: false,
      isListVisible: false,

      Headers: [{
        "COLUMN_NAME": "EPC_TAG",
        "DATA_TYPE": "number",
        "SORTABLE": false,
        "VISIBLE": true,
        "DISPLAY_NAME": this.$t('EPCTag'),
        "DISPLAY_ORDER": 1,
      },
      {
        "COLUMN_NAME": "name",
        "DATA_TYPE": "text",
        "SORTABLE": true,
        "VISIBLE": true,
        "DISPLAY_NAME": this.$t('Name'),
        "DISPLAY_ORDER": 2
      },
      {
        "COLUMN_NAME": "UNIQUE_NAME",
        "DATA_TYPE": "text",
        "SORTABLE": true,
        "VISIBLE": true,
        "DISPLAY_NAME": this.$t('UniqueName'),
        "DISPLAY_ORDER": 3
      },

      {
        "COLUMN_NAME": "AssignedUser",
        "DATA_TYPE": "text",
        "SORTABLE": true,
        "VISIBLE": true,
        "DISPLAY_NAME": this.$t('AssignedTo'),
        "DISPLAY_ORDER": 4,

      },
      {
        "COLUMN_NAME": "number_of_items_assigned",
        "DATA_TYPE": "text",
        "SORTABLE": true,
        "VISIBLE": true,
        "DISPLAY_NAME": this.$t('Quantity'),
        "DISPLAY_ORDER": 5
      },
      {
        "COLUMN_NAME": "Return",
        "DATA_TYPE": "text",
        "SORTABLE": false,
        "VISIBLE": true,
        "DISPLAY_NAME": this.$t('Return'),
        "DISPLAY_ORDER": 6,
        settings: {
          isInSlot: true
        }
      }

      ],

    };

  },
  created: async function () {
    await this.GetPageSize();
    this.GetReturnList();  
  },
  updated:function(){
    $('.expand-list').addClass("hide");
  },
  methods: {
    async GetPageSize(){            
               var vm =this;
                var params='key=DefaultPageSize'
                await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {                  
                if(response.data!=null){                                    
                    vm.PageSize = parseInt(response.data);
                }                   
            });
        },
    async GetReturnList(ths) {      
      if (ths != null) {
        this.chekedInId = 1;
      } else {
        this.chekedInId = "";
      }
      const url = `chekedInId=1&search=${this.search}&pagenumber=${this.pagenumber}&pageSize=${this.pageSize}&sortBy=${this.sortBy}&sortExp=${this.sortExp}`;
      var vueObj = this;
      await DataService.GetRFIDItemsToReturn(url).then((response) => {
        var res = JSON.parse(response.data);
        if (res.data != null) {
          if (res.data.length > 0) {
            vueObj.returnProductData = res.data;
            vueObj.TotalRecords = res.data[0].TOTAL;
            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
            vueObj.CurrentPage = vueObj.pageNum;
            vueObj.noRecord = false;
          } else {
            vueObj.returnProductData = [];
            vueObj.noRecord = true;
            vueObj.TotalRecords = 0;
          }
        } else {
          vueObj.noRecord = true;
          vueObj.returnProductData = [];
          vueObj.TotalRecords = 0;
        }
      });
    },

    SearchData: function () {
        this.GetReturnList("1");

    },

    ResetData: function () {
      this.search = "";
        this.GetReturnList("1");
    },

    pagerMethod: function (value) {
      this.pageSize = value.pageSize;
      this.pageNum = value.pageNum;
      this.GetReturnList();
    },
    sortData: function (value) {
        this.sortBy = value;
        this.sortExp = (this.sortExp == 'DESC') ? 'ASC' : 'DESC'
        this.GetReturnList("1");

    },

    sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

    ReturnList: function () {
      this.search = "";
      this.GetReturnList("1");
    },
      commonHeaderButtonClick: function () { },
      renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                // switch (action.title) {
                //     case 'Edit':
                //         action.isVisible = true;
                //         break;
                // }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
      
  },
};
</script>
